<template>
    <v-container fluid fill-height style="padding:0;" id="touch" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                            <v-card
                                slot-scope="{ hover }"
                                :class="`elevation-${hover ? 12 : 2}`"
                                min-height="200"
                            >
                                <v-card-title>
                                    <h3 id="Touch">touch事件实现画直线</h3>
                                </v-card-title>
                                <v-divider></v-divider>
                                <v-card-text style="position: relative;">
                                    <p class="body-1">1. touch事件是针对手机的触摸点，可以实现多点触控</p>
                                    <p class="body-1">2. 下面代码实现在canvas中利用多个手指同时画直线的功能，请自行在移动设备上运行</p>
                                    <canvas id="canvas" width="600" height="600" style="border:solid black 1px;"></canvas>
                                    <v-flex xs12>
                                        <v-tabs color="light-green darken-1" dark slider-color="yellow">
                                            <v-tab key="js" ripple>JAVASCRIPT</v-tab>
                                            <v-tab key="html" ripple>HTML</v-tab>

                                            <v-tab-item key="js">
                                                <v-card flat>
                                                    <v-card-text>
                                                        <pre class="code">{{pres[0]}}</pre>
                                                    </v-card-text>
                                                </v-card>
                                            </v-tab-item>

                                            <v-tab-item key="html">
                                                <v-card flat>
                                                    <v-card-text>
                                                        <pre class="code">{{pres[1]}}</pre>
                                                    </v-card-text>
                                                </v-card>
                                            </v-tab-item>
                                        </v-tabs>
                                    </v-flex>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
  components: { Blog },
  data() {
    return {
      rect: null,
      time: "2019-5-10",
      title: "利用touch事件实现canvas画板",
      items: [
        {
          title: "touch事件实现画直线",
          target: "#Touch"
        },
        { divider: true },
      ],
      pres: 
[
`
    const canvas = document.getElementsByTagName("canvas")[0];
    const ctx = canvas.getContext("2d");
    let fingerTouchPoints = [];

    // 当用户手指触摸屏幕时触发
    canvas.addEventListener("touchstart", (e) => {
        e.preventDefault();
        const touches = e.changedTouches; // 用户手指触摸点
                
        for (let i = 0; i < touches.length; i++) {                  // 遍历所有触摸点
            const touch = touches[i];
            const cRect = canvas.getBoundingClientRect();           // 获取canvas可视化大小的对象
            const x = Math.round(touch.clientX - cRect.left);       // 获取触摸点相对于canvas的x坐标
            const y = Math.round(touch.clientY - cRect.top);        // 获取触摸点相对于canvas的y坐标

            fingerTouchPoints.push({ 
                color: \`rgb(\${Math.floor(Math.random()*255)}, \${Math.floor(Math.random()*255)},\${Math.floor(Math.random()*255)})\`, 
                pageX: x, 
                pageY: y 
            }); // 将触摸点的信息存在数组中

            ctx.beginPath();
            ctx.arc(x, y, 4, 0, 2 * Math.PI, false);
            ctx.fill(); // 画一个圆点
        } 
    }, false);

    // 当用户手指滑动时触发
    canvas.addEventListener("touchmove", (e) => {
        console.log(e);
        e.preventDefault();
        if(fingerTouchPoints.length === 0) { return; }
        const touches = e.changedTouches;   
        for (let i = 0; i < touches.length; i++) {
            const touch = touches[i];
            const cRect = canvas.getBoundingClientRect(); 
            const x = Math.round(touch.clientX - cRect.left);      
            const y = Math.round(touch.clientY - cRect.top);
            
            ctx.beginPath();
            ctx.moveTo(fingerTouchPoints.slice(i, i+1)[0].pageX, fingerTouchPoints.slice(i, i+1)[0].pageY);
            ctx.lineTo(x, y);
            ctx.strokeStyle = fingerTouchPoints[i].color;
            ctx.lineWidth = 4;
            ctx.stroke();

            // 替换新的触摸点
            fingerTouchPoints.splice(i, 1, { color: fingerTouchPoints[i].color, pageX: x, pageY: y });
        }
    }, false);

    // 当有一个手指松开就触发了
    canvas.addEventListener("touchend", (e) => {
        e.preventDefault();
        fingerTouchPoints = [];
    }, false);

    // 不明因素结束了用户触摸触发
    canvas.addEventListener("touchcancel", (e) => {
        e.preventDefault();
        fingerTouchPoints = [];
    }, false);
`,
`
    <canvas id="canvas" width="600" height="600" style="border:solid black 1px;"></canvas>
`,
]
    };
  },
    mounted() {
        const canvas = document.getElementsByTagName("canvas")[0];
        const ctx = canvas.getContext("2d");
        let fingerTouchPoints = [];
        canvas.addEventListener("touchstart", (e) => {
            e.preventDefault();
            const touches = e.changedTouches; 
            for (let i = 0; i < touches.length; i++) {                  
                const touch = touches[i];
                const cRect = canvas.getBoundingClientRect();           
                const x = Math.round(touch.clientX - cRect.left);       
                const y = Math.round(touch.clientY - cRect.top);        

                fingerTouchPoints.push({ 
                    color: `rgb(${Math.floor(Math.random()*255)}, ${Math.floor(Math.random()*255)},${Math.floor(Math.random()*255)})`, 
                    pageX: x, 
                    pageY: y 
                }); 

                ctx.beginPath();
                ctx.arc(x, y, 4, 0, 2 * Math.PI, false);
                ctx.fill(); 
            } 
        }, false);

        canvas.addEventListener("touchmove", (e) => {
            e.preventDefault();
            if(fingerTouchPoints.length === 0) { return; }
            const touches = e.changedTouches;   
            for (let i = 0; i < touches.length; i++) {
                const touch = touches[i];
                const cRect = canvas.getBoundingClientRect(); 
                const x = Math.round(touch.clientX - cRect.left);      
                const y = Math.round(touch.clientY - cRect.top);
                
                ctx.beginPath();
                ctx.moveTo(fingerTouchPoints.slice(i, i+1)[0].pageX, fingerTouchPoints.slice(i, i+1)[0].pageY);
                ctx.lineTo(x, y);
                ctx.strokeStyle = fingerTouchPoints[i].color;
                ctx.lineWidth = 4;
                ctx.stroke();

                fingerTouchPoints.splice(i, 1, { color: fingerTouchPoints[i].color, pageX: x, pageY: y });
            }
        }, false);

        canvas.addEventListener("touchend", (e) => {
            e.preventDefault();
            fingerTouchPoints = [];
        }, false);

        canvas.addEventListener("touchcancel", (e) => {
            e.preventDefault();
            fingerTouchPoints = [];
        }, false);
    },
    methods: {
    },
    destroyed() {

    },
};
</script>

<style lang="scss">
#touch {
    .blogcontent {
        padding-top: 20px;
    }
    .code {
        font-size: 12px;
        background-color: #f9f9f5;
        white-space: pre-wrap;
        word-wrap: break-word;
        margin-bottom: 15px;
    }                                 
}
